@font-face {
  font-family: "Work Sans";
  font-weight: 400;
  src: url("../fonts/work-sans-regular.woff2") format("woff2"), url("../fonts/work-sans-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  font-weight: 600;
  src: url("../fonts/work-sans-600.woff2") format("woff2"), url("../fonts/work-sans-600.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  font-weight: 700;
  src: url("../fonts/work-sans-700.woff2") format("woff2"), url("../fonts/work-sans-700.woff") format("woff");
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  margin: 0;
  background-color: #f2f2f2; }

a {
  text-decoration: none; }

img {
  width: 100%;
  height: auto; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.page-header {
  background-color: red; }

.page-header h1 {
  margin: 0;
  font-family: "Lucida Grande", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 82px;
  line-height: 97px; }

.logo__wrapper {
  width: 160px;
  height: 160px; }

.about {
  background-color: teal; }

.contacts {
  background-color: blue; }

.events__list {
  padding: 0;
  margin: 0;
  list-style: none; }

.event--active .event__title {
  text-decoration: underline; }

.event__title {
  margin: 0;
  font-family: "Lucida Grande", "Helvetica", sans-serif;
  font-weight: 600;
  font-size: 30px;
  line-height: 35px;
  color: #2f2f2f; }

.event__description {
  margin: 0;
  font-family: "Lucida Grande", "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #6a6a6a; }

.event__tags {
  padding: 0;
  margin: 0;
  list-style: none; }

.event__tag {
  font-family: "Work Sans", "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #c49612; }

.filters {
  background-color: green; }
